import { Component, OnInit } from '@angular/core';
import { ColorService } from '../../color.service';

@Component({
  selector: 'app-theme-control',
  templateUrl: './theme-control.component.html',
  styleUrls: ['./theme-control.component.css']
})
export class ThemeControlComponent implements OnInit {

  theme: any;
  message: any = {
    "basic_text": "#5AA572",
    "primary_text": "#000",
    "secondary_text": "#000",
    "success_text": "#000",
    "info_text": "#000",
    "warning_text": "#000",
    "danger_text": "#000",
    "dark_text": "#000",
    "light_text":"#000",
    "link_text": "#000"
  }

  constructor(private colorService: ColorService) { 
    this.colorService.changeMessage(this.message);
  }

  ngOnInit() {
    this.colorService.currentMessage.subscribe(message => {
      this.message = message;
    });
  }

  ngAfterContentChecked() {
    this.theme = this.message;
    this.colorService.changeMessage(this.theme);
  }

}
